.container{
    width: 100%;
    padding: 5rem 2rem;
    min-height: 80vh;
    display: grid;
    align-items: center;
    background-image: linear-gradient(70deg, var(--yellow),red, tomato);

}
.review-card{
    display: grid;
    grid-template-columns: repeat(auto-fit , minmax(250px , 1fr));
    grid-column-gap: 2rem;
    grid-row-gap: 14rem;
    margin-top: 5rem;
}
.review-card .card{
    background: rgba(255, 255, 255, 0.199);
    border: 1px solid #fff;
    padding: 2rem;
    position: relative;
    height: 220px;
}
.review-card .card img{
    position: absolute;
    top: -50%;
    left: 50%;
    transform: translateX(-50%);
    width: 150px;
    height: 150px;
    object-fit: cover;
    object-position: right;
    border-radius: 50%;
    border-bottom: 1px solid #fff;
    padding: 1rem;
    backdrop-filter: blur(5px);
}
.review-card .inner{
    margin-top: 10rem;
}
.review-card .content{
    position: absolute;
    bottom: 0%;
}
.review-card .content h4{
    text-align: center;
    font-size: 3rem;
    color: #fff;
    margin-bottom: 2rem;
}
.review-card .content p{
    width: 90%;
    margin: auto;
    color: #fff;
    font-size: 1.5rem;
    padding-bottom: 2rem;
}
.review-card .content i{
    color: #fff;
    margin-left: 2rem;
    font-size: 2rem;
    margin-bottom: 2rem;
}

@media(max-width:1270px){
    
    .inner{
        margin-top: 10rem;
    }
}


@media(max-width:770px){
    .inner{
        margin: 0;
    }
    .review-card {
        margin-top: 15rem;
    }
    .review-card .card {
        height: 200px;
    }
    body[data-aos-duration='4000'] [data-aos],
[data-aos][data-aos][data-aos-duration='4000'] {
  transition-duration: 4000ms;
}
}