.contact{
    width: 100%;
    min-height: 80vh;
    background-image: linear-gradient(rgba(0, 0, 0, 0.653),rgb(17, 49, 88)),url(/public/image/contact.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: grid;
    place-items: center;
  }
  
  .inner-contact{
    padding: 2rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
  }
  .inner-contact .content{
    width: fit-content;
    margin: auto;
  }
  .inner-contact .content .box i {
    padding: 1rem;
    background-color: var(--yellow);
    margin-right: 2rem;
  }
  .inner-contact .content .box {
    font-size: 2rem;
    margin: 2rem 0;
    color: #fff;
  }


  @media (max-width:770px){
    .inner-contact{
      grid-template-columns: repeat(1,1fr);
    }
  }