@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,300;0,400;1,400;1,500&display=swap');


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: .3s ease;
  font-family: 'Josefin Sans', sans-serif;
}
a{
  color: #f68800;
  text-decoration: none;
}



.logo a i{
  margin: 0 1rem;
}
 .logo a{
  font-size: 3rem;
  font-weight: bolder;
}


:root{
  --yellow:#f68800;
  --light-teal:#276968;
  --dark-blue:#051721;
  --blue: #0a5697;
  --border:1px solid var(--yellow);
  --fw-light:300;
  --fw-normal:400;
  --fw-bold:500;
  --border-blue:1px solid #0a5697;
}
body{
  scroll-behavior: smooth;
}
html{
  font-size: 62.5%;
}
::-webkit-scrollbar{
  width: 1rem;
}
::-webkit-scrollbar-thumb{
  background: var(--yellow);
}
::-webkit-scrollbar-track{
  background: var(--dark-blue);
}

header{
  background-color: var(--dark-blue);
  width: 100%;
  padding: 1.5rem;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}
.navbar{
  max-width: 1150px;
  margin: auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.navbar ul li{
  list-style: none;
  display: inline-block;
}
.navbar .menu a{
  font-size: 2rem;
  margin: 0 1.5rem;
  padding: .5rem;
  border-radius: .8rem 0 .8rem 0;
  font-weight: bold;
  letter-spacing: 1px;
}
.menu a:hover{
  background: var(--yellow);
  color: var(--dark-blue);
}

.menu-btn i{
  font-size: 3rem;
  color: var(--yellow);
  display: none;
}
.af-heading{
  color: #fff;
  margin: 2rem 0;
  font-size: 1.8rem;
  font-weight: normal;
  position: relative;
  width: fit-content;
}
.af-heading::after{
  content: "";
  position: absolute;
  bottom: -10%;
  left: 0;
  width: 70%;
  height: .2rem;
  background-color: #fff;
}


/* main section start ============= */
.main{
  width: 100%;
  min-height: 80vh;
  background: linear-gradient(rgba(0, 0, 0, 0.85),rgba(5, 5, 5, 0.503)) , url(../public/image/building.jpg);
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  object-fit: cover;
  display: grid;
  grid-template-columns: 2fr 1fr;
  place-items: center;
  padding: 1rem 0;
}
.main .left-main h2::first-letter{
  color: var(--yellow);
  font-size: 4rem;
}
.main .left-main h2{
  font-size: 3rem;
  color: white;
  width: fit-content;
  margin: 3em auto 0;
  font-weight: 400;
}
.main .left-main p{
  min-width: 30rem;
  max-width: 50rem;
  color: #fff;
  margin-bottom: 2rem;
  transition: none;
  padding: 1rem;
  font-size: 1.4rem;
  font-weight: 300;
}
.main .main-links{
  width: 30rem;
  height: 30rem;
  background: linear-gradient(rgba(0, 0, 0, 0.483),rgba(0, 0, 0, 0.401)) , url(../public/image/3d\ building\ in\ hand.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  margin: auto;
  display: flex;
  justify-content: space-evenly;
  align-items: flex-end;
  padding: 1rem 0;
}
.main .main-links span{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: rgba(0, 128, 128, 0.354);
  padding: .5rem;
  border: var(--border);
}
.main .main-links span:hover {
  background-color: var(--yellow);
  color: var(--dark-blue);
}
.main .main-links span i{
  font-size: 3rem;
  color: var(--yellow);
}
.main-links span:hover i{
  color: var(--dark-blue);
}
.main .main-links span h4{
  font-size: 1.2rem;
  color: white;
  margin-top: 1rem;
  font-weight: 400;
}



/* ============= about page styling ==================== */
  .about{
    width: 100%;
    min-height: 100vh ;
    display: grid;
    place-items: center;
    background-image: linear-gradient(#051721,rgba(15, 18, 79, 0.66)),url(../public/image/building.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    backdrop-filter: blur(5px);
  }
  .inner-about{
    background-color: #051721;
    padding: 3rem;
    color: #ffff;
  }
.inner-about h2{
  font-size: 5rem;
}
/* ============= about page styling end ==================== */

/* =================footer styling ============ */
.footer{
  background-color: var(--dark-blue);
  padding: 2rem;
}

.footer .logo{
  margin-bottom: 1rem;
}

.footer-box{
  display: grid;
  grid-template-columns: repeat(3,1fr);
}


.foots {
  width: fit-content;
}
.footer-box .foots h4{
  color: #fff;
  margin: 2rem 0;
  font-size: 1.8rem;
  font-weight: normal;
}
.footer-box ul li{
  list-style: none;
  margin: 1rem 0;
  font-size: 1.5rem;
}
.footer-box .foots ul li:hover{
  /* opacity: 50%; */
  transform: translateX(10px);
}

.foots .service-address::before{
  content: '';
  background-color: var(--yellow);
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
}
.foots .service-address{
  width: fit-content;
  margin: 2rem 0;
  padding: 2rem;
  background-color: rgba(255, 255, 255, 0.068);
  backdrop-filter: blur(5px);
  border-radius: 3rem 0 3rem 0;
  color: #fff;
  font-size: 1.5rem;
  position: relative;
  z-index: 10;
  overflow: hidden;
}
.foots .service-address::before{
  content: '';
  background-color: var(--yellow);
  width: 20rem;
  height: 20rem;
  border-radius: 50%;
  position: absolute;
  z-index: -10;
  left: 60%;
  opacity: 50%;
}
.foots .service-address span{
  color: var(--yellow);
  font-weight: normal;
}
.foots .social-icons {
  display: flex;
  justify-content: space-evenly;
}
.foots .social-icons a{
  font-size: 3rem;
}




/* media queries============= */

@media(max-width:900px){

  html{
    font-size: 50%;
  }
  
}


@media(max-width:750px){
  .main{
    grid-template-columns: 1fr;
    margin-top: 5rem;
  }


.menu{
  position: absolute;
  top: 100%;
  width: 100%;
  background: var(--dark-blue);
  border-top: 1px solid var(--yellow) ;
  height: 0;
  overflow: hidden;
}
.menu ul {
  width: fit-content;
  margin: auto;
}
.menu ul li{
  display: block;
  margin: 2rem 0;
}

.menu-btn i{
  display: block;
}
  
.main .left-main h2{
  font-size: 2.5rem;
  margin-top: 2rem;
}

.main .right-main form{

  width: 40rem;
}
  
.footer-box{
  grid-template-columns: repeat(2,1fr);
  grid-template-rows: repeat(2,1fr);
}
.foots:nth-child(3){
  grid-column: span 2;
}





}



