.btn{
    font-size: 2rem;
    background: transparent;
    color: #fff;
    border: var(--border);
    padding: .5rem 1rem;
    border-radius: .4rem;
    cursor: pointer;
  }
  .btn:hover{
    background-color: var(--yellow);
    color: white;
  }
   .btn i{
  margin: 0 1rem;
  }
  
  .contact form .btn{
    font-size: 2rem;
    background: transparent;
    color: #fff;
    border: var(--border-blue);
    padding: .5rem 1rem;
    border-radius: .4rem;
    cursor: pointer;
  }
  .contact form .btn:hover{
    background-color: var(--blue);
    color: white;
  }