.cont-form{
    width: 30rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: var(--border);
    padding: 1rem;
    border-radius: 1rem;
    background: #c0cbd10e;
    backdrop-filter: blur(8px);
    position: relative;
    overflow: hidden;
    z-index: 1;
    margin: auto;
  }
  .cont-form::before{
    content: '';
    position: absolute;
    width: 50rem;
    height: 50rem;
    top: -50;
    left: 80%;
    border-radius: 50%;
    background-color: var(--yellow);
    background-color: white;
    opacity: 50%;
    z-index: -1;
  }
  
  .cont-form h4{
    font-size: 2rem;
    color: white;
    border-bottom: 2px solid #fff;
    font-weight: normal;
  }
  .cont-form input{
    background-color: transparent;
    padding: .5rem 1rem;
    width: 100%;
    border: none;
    border-bottom: var(--border);
    outline: none;
    margin: 1rem 0;
    color: white;
  }
  .cont-form input::placeholder{
    color: #fff;
  }

  .inner-contact .cont-form input{
    border-bottom:  var(--border-blue);
  }
  .inner-contact .cont-form{
    border: var(--border-blue);
  }